body{

  background-color: #f2f2f2;
}
.App {
  text-align: center;
  max-width: 1280px;
  margin: 0 auto;
}
.main-nav ul{
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
}
.main-nav ul li{
  font-size: 1.2rem;
  margin: 0 20px;
}
a{
  text-decoration: none;

}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
